@import '../../styles/variables';
@import '../../styles/mixins';

.footer {
  background: $black;
  padding: 24px 0;

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px;
  }

  &__text {
    color: $lime;
    font-size: 18px;
    font-weight: 800;
    line-height: 28px;
  }
} 